import React from 'react'
import './style.css';
import { BsFillCameraVideoFill } from 'react-icons/bs';

const Dross = () => {
    return (
        <div className='Dross'>
            <div className='container'>
                    <h3 className='bgp'>دروس  تعليمية للبرنامج : <BsFillCameraVideoFill/></h3>
                <div className="boxvideo">

    <iframe title="video1" className='vyoutube'  width="300" height="300" src="https://www.youtube.com/embed/-ILqIziPpVI" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe> 

    <iframe title="video2" className='vyoutube'  width="1000" height="1000" src="https://www.youtube.com/embed/FTjebODl6nI" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
             
              </div>
        </div>
    )
}

export default Dross
